$small: 767px;

@media only screen and (max-width: $small) {
  .def__tag,
  .def__extender,
  .component__signup {
    display: none; }

  .popup {
    background-color: $white;
    border-radius: 0;
    margin-top: 0; }

  .popup__content {
    margin: 0; }

  .popup__def {
    justify-content: center;
    margin-bottom: 15px; }

  .def {
    font-size: 18px; }


  .dismiss {
    position: absolute;
    right: 0;
    margin-right: 15px;
    width: 20px;
    background-color: $dismiss;
    opacity: 0.54;
    &::after {
      width: 20px;
      background-color: $dismiss; } }

  .component {
    flex: 1 1 100%;
    justify-content: center;
    margin-left: 15px;
    margin-right: 15px; } }
