@import "variables";

.component {
  display: flex;
  padding: 16px 20px 16px 0;
  margin-bottom: 10px;
  border-radius: 3px;
  border: 1px solid $border;
  background-color: $white;
  //
  flex: 1 1 calc(50% - 10px);
  align-items: center;
  justify-content: space-between;
  margin-left: 5px;
  margin-right: 5px; }

.component--hide {
  display: none; }

.component__extnd {
  flex: 0 1 100%; }

.component__title, .signup__title {
  margin: 6px 0;
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase; }

.component__desc {
  font-size: 13px;
  line-height: 17px; }

.component__tag {
  display: flex;
  flex: 0 0 44px;
  margin: auto 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.signup__tag {
  flex: 0 0 44px;
  display: flex;
  align-items: center;
  flex-direction: column; }

.signup__title {
  text-align: center;
  margin: 0 auto;
  padding-top: 6px; }

.component__def {
  display: flex;
  flex-direction: column; }

.component__text {
  font-size: 13px;
  line-height: 17px;
  color: $text; }

.component__result {
  font-family: 'Sacramento', cursive;
  font-size: 30px;
  margin: 0;
  padding: 0;
  height: 71px;
  width: 173px;
  justify-content: center; }
